<template>
    <el-row type="flex" justify="space-between" class="header">
        <el-col :span="12">
            <div class="title">云南土特产管理系统</div>
        </el-col>
        <el-col :span="6" v-if="showInput">
          <el-input
            @keyup.enter.native="changeName"
            clearable
            size="medium"
            placeholder="请输入内容"
            prefix-icon="el-icon-search"
            v-model="name">
          </el-input>
        </el-col>
        <el-col :span="6" v-else></el-col>
        <el-col :span="4" style="text-align: right">
            <div class="user" v-if="userName">
              Hello, <span class="name">{{userName}}</span>
              <span class="logout" @click="logout">
                <img style="height: 16px;" src="../assets/img/logout.png">
              </span>
            </div>
            <div class="login" v-else>
              <span class="goLogin" @click="goLogin">
                <img style="height: 16px;" src="../assets/img/login.png">
              </span>
            </div>
        </el-col>
        <el-col :span="2" style="text-align: right">
            <div class="change">
              <span class="changePath" @click="changePath">
                {{showInput ? '[ 管理页 ]' : '[ 展示页 ]'}}
              </span>
            </div>
        </el-col>
    </el-row>
</template>

<script>
  import axios from 'axios'
  import { mapState, mapActions } from 'vuex'
    export default {
        name: "PublicHeader",
        props: {
          showInput: Boolean
        },
        data() {
          return {
            name: ''
          }
        },
        computed: {
          ...mapState(['userName'])
        },
        watch: {
          name(val) {
            if (!val) {
              this.$emit('changeName', val)
            }
          }
        },
        methods: {
          changeName() {
            this.$emit('changeName', this.name)
          },
          changePath() {
            if (this.showInput) {
              this.$router.push('home')
            } else {
              this.$router.push('manage')
            }
          },
          goLogin() {
            this.$router.push('login')
          },
          logout() {
            this.$confirm('是否退出登录?')
              .then(() => {
                this.getLogout()
              }).catch(() => {

            })
          },
          getLogout() {
            axios.post('/user/logout', {})
              .then((res) => {
                const data = res.data
                if (data.code === 70000) {
                  this.$message({
                    message: '您已注销',
                    type: 'success'
                  });
                  this.setUser('')
                  this.$router.push('login')
                }
              })
          },
          ...mapActions(['setUser'])
        }
    }
</script>

<style scoped>
.header {
    line-height: 60px;
}
.title {
    font-size: 28px;
    font-weight: bold;
    color: #67C23A;
}
.user {
    font-size: 16px;
    color: #666;
}
.name {
    color: #409EFF;
}
.logout {
    color: #F56C6C;
    cursor: pointer;
}
.login {
    font-size: 14px;
    color: #409EFF;
}
.goLogin {
    cursor: pointer;
}
.change {
    font-size: 14px;
    color: #409EFF;
}
.changePath {
    cursor: pointer;
}
</style>



